<template>
  <div>
    <!--面包屑导航区域-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">我的信息</a></el-breadcrumb-item>
      <el-breadcrumb-item>基本信息</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card height="100%" align="left">
      <el-row :gutter="20">
        <el-col :span="12" :offset="9">
          <div class="grid-content">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  id:    {{administrator.id}}
            </span>
          </div>
        </el-col>
        <el-col :span="12" :offset="9">
          <div class="grid-content">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  用户名:    {{administrator.username}}
            </span>
          </div>
        </el-col>
        <el-col :span="12" :offset="9">
          <div class="grid-content bg-purple">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  审查职权:    {{administrator.verifyRange}}
            </span>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      administrator: {
        id: 123,
        username: '1234',
        password: '1234',
        verifyRange: '商城'
      }
    }
  },
  created () {
    this.getAdminInfo()
  },
  methods: {
    async getAdminInfo () {
      const { data: res } = await this.$http.get('admin', { params: this.$store.state.user.username })
      if (res.code !== 200) {
        this.$message.error('获取用户信息失败')
        await this.$router.push('/welcome')
      } else {
        this.administrator = res.data
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>
