<template>
  <div>
    <!--面包屑导航区域-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">我的信息</a></el-breadcrumb-item>
      <el-breadcrumb-item>基本信息</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card height="100%" align="left">
      <el-row :gutter="20" align="left">
        <el-col :span="12" :offset="9">
          <div class="grid-content">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  id:    {{developer.id}}
            </span>
          </div>
        </el-col>
        <el-col :span="12" :offset="9">
          <div class="grid-content">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  用户名:    {{developer.devUsername}}
            </span>
          </div>
        </el-col>
        <el-col :span="12" :offset="9">
          <div class="grid-content bg-purple">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  昵称:    {{developer.devNickname}}
            </span>
          </div>
        </el-col>
        <el-col :span="12" :offset="8">
          <div class="grid-content bg-purple">
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  基本信息:    {{developer.devInfo}}
            </span>
          </div>
        </el-col>
        <el-col :span="12" :offset="8">
          <div class="grid-content bg-purple">
            <el-avatar fit="fill" src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png">
            </el-avatar>
            <span>  应用清单:
              <ul v-for="(item, index) in developer.appList" :key="index">
                <li>
                  {{item}}
                </li>
              </ul>
            </span>
          </div>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      developer: {
        id: 123,
        devUsername: '001',
        devNickname: '天天爆肝',
        devInfo: '学习使人消瘦',
        appList: ['aaa', 'ccc', 'bbb']
      },
      app1: {
        name: 'aaa',
        state: true
      },
      app2: {
        name: 'bbb',
        state: true
      },
      app3: {
        name: 'ccc',
        state: true
      },
      appListsName: '',
      pic: '/platform/src/assets/heima.png'
    }
  },
  created () {
    this.getDevInfo()
    this.catAppList()
  },
  methods: {
    catAppList () {
      for (let item in this.developer.appList) {
        this.appListsName += item.name
        this.appListsName += ' '
      }
      console.log(this.appListsName)
    },
    async getDevInfo () {
      const { data: res } = await this.$http.get('deve', { params: this.$store.user.username })
      if (res.code !== 200) {
        this.$message.error('获取用户信息失败')
        await this.$router.push('/welcome')
      } else {
        this.developer = res.data
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  /* .bg-purple {
    background: #d3dce6;
  } */
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>
