<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">审核列表</a></el-breadcrumb-item>
      <el-breadcrumb-item>待审核应用</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table :data="appLists" empty-text="暂无需要审核的应用">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="应用Id">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label="应用类型">
                <span>{{ props.row.types }}</span>
              </el-form-item>
              <el-form-item label="超链接">
                <span>{{ props.row.appBasic.url }}</span>
              </el-form-item>
              <el-form-item label="更新时间">
                <span>{{ props.row.appVersion.updateTime }}</span>
              </el-form-item>
              <el-form-item label="基础信息说明">
                <span>{{ props.row.appBasic.basicInformation }}</span>
              </el-form-item>
              <el-form-item label="版本信息说明">
                <span>{{ props.row.appVersion.versionInformation }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="应用名称" prop="appName">
        </el-table-column>
        <el-table-column label="应用作者" prop="appBasic.author">
        </el-table-column>
        <el-table-column label="应用版本" prop="appVersion.versionId">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.appBasic.examine" active-text="通过" inactive-text="未审核"
                       active-value="1" inactive-value="0" @change="submitCensor(scope.row)" :disabled="disable1">
            </el-switch>
            <el-switch v-model="scope.row.appBasic.examine" active-text="不通过" inactive-text="未审核"
                       active-value="2" inactive-value="0" @change="submitCensor(scope.row)" :disabled="disable2">
            </el-switch>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination :total="total" layout="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      appLists: [ {
        id: '001',
        appName: 'WeChat',
        types: '社交',
        appBasic: {
          id: '001',
          name: 'WeChat',
          author: 'Tencent',
          state: '0',
          examine: '0',
          basicInformation: '微信',
          url: 'www.dev.com'
        },
        appVersion: {
          id: '001',
          name: 'WeChat',
          versionId: '7.0.10',
          versionInformation: '现已完成漏洞修补',
          updateTime: '2020-02-02 15:24:33'
        }
      } ],
      disable1: false,
      disable2: false,
      total: 0
    }
  },
  created () {
    this.getAppLists()
  },
  methods: {
    async getAppLists () {
      const { data: res } = await this.$http.get('/censor')
      if (res.meta.status !== 200) return this.$message.error('获取列表信息失败')
      this.appLists.push(res.data)
      this.total = this.appLists.length
      this.$message.success('获取列表信息成功')
    },
    submitCensor (app) {
      if (app.appBasic.examine === '0') {
        this.disable2 = false
        this.disable1 = false
      } else if (app.appBasic.examine === '1') {
        this.disable2 = true
        const param = {
          id: app.id,
          examine: app.appBasic.examine
        }
        const { data: res } = this.$http.post('/censor', { params: param })
        if (res.code !== 200) return this.$message.error('更新审核状态失败')
        this.$message.success('更新审核状态成功')
        console.log(res)
      } else {
        this.disable1 = true
        const param = {
          id: app.id,
          examine: app.appBasic.examine
        }
        const { data: res } = this.$http.post('/censor', { params: param })
        if (res.code !== 200) return this.$message.error('更新审核状态失败')
        this.$message.success('更新审核状态成功')
        console.log(res)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .demo-table-expand label {
    width: 90px;
    color: #8492a6;
  }
</style>
